.flightinfo {
    width: 100%;
    height: 100%;
    padding: 0.5rem;
    .toggle-sidebar {
        background-color: inherit;
        color: black;
        outline: none;
        border: none;
        box-shadow: 0px 0px 4px 4px #eee;
        margin: 3rem 0rem;
        font-size: 15px;
        // display: none
        @include mainTransition;
        @media (max-width: $phone) {
            height: 23px;
            font-size: 12px;
            padding: 11px 5px;
            display: flex;
            justify-content: center;
            align-items: center; }
        @media (min-width: $phone) {
            height: 23px;
            font-size: 12px;
            padding: 11px 5px;
            display: flex;
            justify-content: center;
            align-items: center; }
        @media (min-width: $disktop) {
            display: none; }
        i {
            @include mainTransition;
            transform: rotate(180deg); }
        &.active {
            i {
                transform: rotate(-180deg); } } }
    hr {
        border-top: 1px solid #e4dbdb; }
    .main-header {
        display: grid;
        grid-template-columns: 10% 4% 11% 12% 15% 14% 35%;
        align-items: center;
        @media (max-width: $phone) {
            grid-template-columns: 13% 3% 15% 18% 16% 16% 19%;
            font-size: 8px; }
        @media (min-width: $phone) {
            grid-template-columns: 13% 3% 15% 18% 16% 16% 19%;
            font-size: 8px; }
        @media (min-width: $tablet) {
            grid-template-columns: 10% 4% 11% 12% 15% 14% 35%;
            font-size: 13px; }
        .desc,.info {
            display: block; }
        .desc,i {
            font-size: 16px;
            font-weight: 600;
            color: #a9a9a9;
            @media (max-width: $phone) {
                font-size: 6px; }
            @media (min-width: $phone) {
                font-size: 6px; }
            @media (min-width: $tablet) {
                font-size: 16px; } }
        .modify-search {
            align-self: center;
            display: block;
            width: 40%;
            background-color: #6488c0;
            margin-left: auto;
            text-align: center;
            margin-right: 10px;
            color: #fff;
            font-size: 15px;
            padding: 0.5rem 0rem;
            border-radius: 3px;
            outline: none;
            @media (max-width: $phone) {
                width: 100%;
                font-size: 7px;
                padding: 0px 0px;
                height: 20px; }
            @media (min-width: $phone) {
                width: 100%;
                font-size: 7px;
                padding: 0px 0px;
                height: 20px; }
            @media (min-width: $tablet) {
                font-size: 15px;
                height: 30px; }
            @media (min-width: $disktop) {
                width: 40%; } } }
