* {
    margin: 0rem;
    padding: 0rem;
    box-sizing: border-box; }
body {
    background-color: $body_bgColor !important;
    font-family: 'Poppins', sans-serif !important;
    padding: 0rem !important;
    margin: 0rem !important;
    overflow-x: hidden;
    &.no-scroll {
        overflow: hidden; } }
ul,ol {
    list-style: none;
    margin: 0rem; }
.black {
    color: #000 !important; }
.border-l-none {
    border-left: none !important; }
#main-content {
    margin-top: 6%; }
.content {
    .window {
        position: relative;
        .sidebar-window {
            width: 356px;
            display: inline-block;
            @include mainTransition;
            @media (max-width: $phone) {
                width: 100%; }
            @media (min-width: $phone) {
                width: 100%; }
            @media (min-width: $Stablet) {
                width: 100%; }
            @media (min-width: $disktop) {
                width: 356px; }
            &.no-sidbar {
                transform: translateX(-120%);
                width: 0%;
                .sidebar {
                    display: none;
                    pointer-events: none; } } }
        .flight-info-window {
            width: calc(100% - 356px);
            display: inline-block;
            float: right;
            opacity: 0;
            pointer-events: none;
            @include mainTransition;
            @media (max-width: $phone) {
                .flightinfo {
                    display: none; } }
            @media (min-width: $phone) {
                .flightinfo {
                    display: none; } }
            @media (min-width: $disktop) {
                width: calc(100% - 356px);
                opacity: 1;
                pointer-events: all;
                .flightinfo {
                    display: block; } }
            &.no-sidbar {
                width: 100%;
                opacity: 1;
                pointer-events: all;
                @media (max-width: $phone) {
                    .flightinfo {
                        display: block; } }
                @media (min-width: $phone) {
                    .flightinfo {
                        display: block; } } } } } }
