header {
    background-color: $header_bgColor;
    @media (max-width: $phone) {
        padding: 0rem 1rem; }
    @media (min-width: $Stablet) {
        padding: 1rem 1rem; }
    @media(min-width: $disktop) {
        padding: 0rem 4rem; }
    .navbar {
        display: grid;
        align-items: center;
        justify-items: center;
        align-items: center;
        grid-template-columns: 18% 1fr;
        padding: 0rem;
        @media (max-width: $phone) {
            padding: 10px 0px; }
        @media (min-width: $phone) {
            padding: 1rem 0rem; }
        .humburger-btn {
            background-color: $second_color;
            height: auto;
            padding: 2px 7px;
            border: none;
            align-items: center;
            border-radius: 3px;
            outline: none;
            display: none;
            @media (max-width: $phone) {
                display: block;
                margin-left: auto;
                padding: 0px 5px; }
            @media (min-width: $phone) {
                display: block;
                margin-left: auto;
                padding: 0px 5px; }
            @media (min-width: $Stablet) {
                display: block;
                margin-left: auto;
                padding: 0px 3px; }
            @media (min-width: $tablet) {
                display: block;
                margin-left: auto;
                padding: 1px 5px; }
            @media (min-width: $disktop) {
                display: none; }
            span {
                background-color: #fff;
                width: 40px;
                height: 2px;
                display: block;
                margin: 6px 0px;
                @media (max-width: $phone) {
                    width: 36px;
                    height: 1.5px;
                    margin: 6px auto; } } }
        .logo {
            text-align: center;
            display: flex;
            align-items: center;
            @media (max-width: $phone) {
                font-size: 15px; }
            @media (min-width: $phone) {
                font-size: 20px; }
            @media (min-width: $Stablet) {
                font-size: 22px; }
            @media (min-width: $tablet) {
                font-size: 30px; }
            @media (min-width: $disktop) {
                font-size: 35px; }
            @media (min-width: $LGdesktop) {
                font-size: 3rem; } }
        .list-item {
            color: $header_color;
            width: 65%;
            margin-left: auto;
            @media ( max-width: $phone ) {
                display: none; }
            @media (min-width: $phone) {
                display: none; }
            @media (min-width: $Stablet) {
                display: none; }
            @media (min-width: $tablet) {
                display: none; }
            @media (min-width: $disktop) {
                display: block;
                width: 66%; }
            @media (min-width: $LGdesktop) {
                display: block;
                width: 66%; }
            .item {
                position: relative;
                height: 100%;
                align-items: center;
                padding: 1rem 1rem;
                font-size: 15px;
                cursor: pointer;
                display: inline-block;
                @include mainTransition;
                @media (min-width: $LGdesktop) {
                    font-size: 20px; }
                i {
                    position: absolute;
                    bottom: 38%;
                    right: 0%;
                    font-size: 13px; }
                .contact-us {
                    text-decoration: none;
                    color: $header_color;
                    @include mainTransition;
                    &:hover {
                        color: $second_color; } }
                button {
                    background-color: $signin_color;
                    color: $main_color;
                    border: none;
                    padding: 10px 55px 30px;
                    font-size: 14px;
                    outline: none;
                    border-radius: 4px;
                    cursor: pointer;
                    @include mainTransition;
                    @media ( min-width: $tablet ) {
                        padding: 10px 31px 21px;
                        font-size: 13px; }
                    &:hover {
                        background-color: $signin_Hovercolor; } }
                .dropdown-menu {
                    background-color: $preloader_color;
                    position: absolute;
                    top: 84%;
                    left: 24%;
                    width: 122px;
                    padding: 13px 8px 21px 12px;
                    font-size: 13px;
                    border-radius: 3px;
                    display: none;
                    a {
                        color: $main_color;
                        text-decoration: none; }
                    &::before {
                        content: "";
                        top: -20%;
                        left: 13px;
                        position: absolute;
                        border: 10px solid;
                        border-color: transparent transparent $second_color transparent; }
                    li {
                        margin: 6px 0rem 0px 0px; } }
                &:hover {
                    color: $second_color; }
                &.signIn-btn {
                    &:hover {
                        background-color: $header_bgColor; } }
                &.active {
                    color: $second_color;
                    .dropdown-menu {
                        display: block; } } } } }
    .mopile-list {
        padding: 1rem 0rem;
        display: none;
        .nav-list {
            list-style: none;
            text-align: center;
            .nav-item {
                color: #44494e;
                display: inline-block;
                margin: 10px 0px;
                width: 13%;
                font-size: 15px;
                cursor: pointer;
                position: relative;
                @media (max-width: $phone) {
                    width: 70%; }
                @media (min-width: $phone) {
                    width: 70%; }
                @media (min-width: $Stablet) {
                    width: 22%;
                    font-size: 11px; }
                @media (min-width: $tablet) {
                    width: 24%;
                    font-size: 12px; }
                a {
                    color: #44494e;
                    text-decoration: none; }
                i {
                    position: absolute;
                    top: 20%;
                    right: 25%;
                    @media (max-width: $phone) {
                        right: 28%; }
                    @media (min-width: $phone) {
                        right: 30%; }
                    @media (min-width: $Stablet) {
                        right: 22%; } }
                .dropdown-menu {
                    background-color: $preloader_color;
                    position: absolute;
                    top: 100%;
                    left: 37%;
                    width: 122px;
                    padding: 13px 8px 21px 12px;
                    font-size: 13px;
                    border-radius: 3px;
                    display: none;
                    a {
                        color: $main_color;
                        text-decoration: none; }
                    &::before {
                        content: "";
                        top: -20%;
                        left: 13px;
                        position: absolute;
                        border: 10px solid;
                        border-color: transparent transparent $second_color transparent; }
                    li {
                        margin: 6px 0rem 0px 0px; } }
                &:hover {
                    color: $second_color; }
                &.active {
                    color: $second_color;
                    .dropdown-menu {
                        display: block; } } }
            .signIn-btn {
                width: 100%;
                display: block;
                button {
                    background-color: #6598e6;
                    color: #fff;
                    font-size: 13px;
                    display: block;
                    width: 20%;
                    border: none;
                    margin: auto;
                    @media (max-width: $phone) {
                        width: 70%; } } } } } }
.RetrieveMyBooking-suppage {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0%;
    left: 0%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #7e7e7e69;
    z-index: 99999;
    opacity: 0;
    pointer-events: none;
    @include mainTransition;
    .con-btn {
        .close-btn {
            display: block;
            margin-left: auto;
            border: none;
            color: $main_color;
            background-color: $second_color;
            padding: .2rem 1rem;
            cursor: pointer;
            outline: none;
            font-size: 18px; }
        .content {
            border-top: 3px solid $second_color;
            background-color: #fff;
            width: 422px;
            padding: 2rem 1rem;
            @media (max-width: $phone) {
                width: 270px; }
            @media (min-width: $phone) {
                width: 270px; }
            @media (min-width: $Stablet) {
                width: 400px; }

            h2 {
                font-size: 2rem;
                @media (max-width: $phone) {
                    font-size: 20px; }
                @media (min-width: $phone) {
                    font-size: 20px; }
                @media (min-width: $Stablet) {
                    font-size: 2rem; } }
            form {
                label {
                    margin: 1rem 0rem; }
                input {
                    display: block;
                    width: 100%;
                    font-weight: bold; }
                p {
                    font-size: 12px;
                    margin: 1rem 0rem 0rem;
                    @media (max-width: $phone) {
                        font-size: 9px; }
                    @media (min-width: $phone) {
                        font-size: 9px; }
                    @media (min-width: $Stablet) {
                        font-size: 12px; } }
                button {
                    display: block;
                    width: 100%;
                    color: $second_color;
                    border: 1px solid $second_color;
                    background-color: $main_color;
                    margin-top: 2rem;
                    padding: .5rem 0rem;
                    outline: none;
                    cursor: pointer;
                    @include mainTransition;
                    &:hover {
                        background-color: $second_color;
                        color: $main_color; } } } } }
    &.active {
        opacity: 1;
        pointer-events: all; } }
.sign-in {
    position: absolute;
    top: 0%;
    left: 0%;
    right: 0%;
    bottom: 0%;
    width: 100%;
    height: 100%;
    opacity: 0;
    pointer-events: none;
    background-color: #7e7e7e69;
    overflow-x: hidden;
    z-index: 9999;
    @include mainTransition;
    .toggle-menu {
        position: absolute;
        background-color: $main_bgColor;
        height: 55rem;
        width: 30%;
        right: 0%;
        padding: 1rem;
        transform: translateX(100%);
        pointer-events: none;
        @include mainTransition;
        @media (max-width: $phone) {
            width: 100%; }
        @media (min-width: $phone) {
            width: 100%; }
        @media (min-width: $tablet) {
            width: 60%; }
        @media (min-width: $disktop) {
            width: 30%; }
        .header {
            a {
                text-decoration: none;
                color: $header_color; }
            i {
                float: right;
                cursor: pointer;
                font-size: 20px; } }
        .info {
            background: linear-gradient(-290deg, rgb(17, 85, 139) -46%, rgb(54, 101, 172) -14%, rgb(89, 146, 232) 99%);
            color: $main_color;
            padding: 1rem;
            margin-top: 1rem;
            border-radius: 5px;
            h2 {
                font-size: 22px;
                font-weight: 600; } }
        .box {
            margin-top: 2rem;
            h2 {
                text-align: center;
                font-size: 22px; }
            .facebook {
                display: block;
                width: 100%;
                background-color: #192a56;
                color: #fff;
                text-align: center;
                padding: .5rem 0rem;
                font-size: 18px;
                border-radius: 2px;
                margin: 2rem 0rem;
                text-decoration: none; }
            .or {
                position: relative;
                hr {
                    border-top: 2px solid rgba(0,0,0,0.1); }
                span {
                    color: #0000005c;
                    position: absolute;
                    bottom: -0.5rem;
                    right: 48%;
                    background-color: #fff; } }
            .options {
                position: relative;
                li {
                    display: inline-block;
                    cursor: pointer;
                    width: 49%;
                    text-align: center;
                    padding: 1rem 0rem;
                    i {
                        padding-right: 1rem; }
                    &.active {
                        border-bottom: 3px solid #192a56; } }
                hr {
                    position: absolute;
                    width: 100%;
                    top: 68%;
                    border-top: 3px solid #00000026; } }
            form {
                margin-top: 2rem;
                label {
                    margin-top: 2rem; }
                input {
                    display: block;
                    width: 100%;
                    outline: #192a56; }
                button {
                    display: block;
                    width: 100%;
                    margin: 2rem 0rem;
                    border: 1px solid $second_color;
                    outline: none;
                    background-color: $second_color;
                    color: $main_color;
                    padding: 1rem 0rem 2rem;
                    border-radius: 2px;
                    cursor: pointer; }
                .mobile-number {
                    .iti--allow-dropdown {
                        display: block;
                        width: 100%; } }
                .email-addres,.mobile-number {
                    display: none;
                    &.active {
                        display: block; } } }
            .forgot-password {
                display: block;
                width: 100%;
                text-align: center;
                color: #192a56; }
            .dont-haveAccount {
                margin: 2rem 0rem;
                a {
                    margin-left: 8px;
                    text-decoration: none;
                    color: #192a56; } } } }
    &.active {
        opacity: 1;
        pointer-events: all;
        .toggle-menu {
            transform: translateX(0%);
            pointer-events: all; } } }
